import * as React from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Seo from '../components/seo';
import Section from '../components/section';
import SectionMenu from '../components/sections/menu';
import SectionMainvisual from '../components/sections/mainvisual';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';

import TextField from '@mui/material/TextField';

import { Footer } from '../components/sections/footer';

const theme = createTheme({
  palette: {
    primary: {
      main: '#235784',
      light: '#235784',
      dark: '#ffffff'
    }
  },
  typography: {
    fontFamily: [
      'Helvetica Neue',
      'Arial',
      'Hiragino Kaku Gothic ProN',
      'Hiragino Sans',
      'Meiryo',
      'sans-serif'
    ].join(','),
    h1: {
      fontSize: 40
    },
    h2: {
      fontSize: 32,
      fontWeight: 300,
      letterSpacing: 8
    },
    h3: {
      fontSize: 28,
      fontWeight: 300,
      letterSpacing: 4
    },
    h4: {
      fontSize: 24
    },
    h5: {
      fontSize: 20
    }
  }
});

const IndexPage = () => {
  const [openingAnimation, setOpeningAnimation] = React.useState(0);
  const [isShow, setIsShow] = React.useState(false);

  React.useEffect(() => {
    // @TODO Opening animation
    const { searchParams } = new URL(window.location.href);
    const openingAnimation = Number(searchParams.get('openingAnimation'));

    if (!!openingAnimation) {
      setTimeout(() => {
        setIsShow(true);
      }, 500);
    } else {
      setIsShow(true);
    }

    setOpeningAnimation(openingAnimation);
  }, []);

  const SectionTitle = ({ children, variant = 'h2', align = 'left', sub }) => (
    <Box mb={variant === 'h2' ? 8 : 4}>
      <Typography
        variant={variant}
        align={align}
        sx={{
          letterSpacing: 8
        }}
      >
        {children}
      </Typography>
      {sub && (
        <Typography
          align={align}
          sx={{
            letterSpacing: 4
          }}
        >
          {sub}
        </Typography>
      )}
    </Box>
  );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {openingAnimation === 1 && (
        <>
          <div
            style={{
              position: 'fixed',
              top: 0,
              left: 0,
              width: '100%',
              height: '50vh',
              boxSizing: 'border-box',
              borderBottom: '1px solid #b28223',
              backgroundColor: '#000',
              transformOrigin: 'top',
              transition: 'transform 500ms ease-out',
              transform: `scaleY(${isShow ? 0 : 1})`,
              zIndex: 2000
            }}
          />
          <div
            style={{
              position: 'fixed',
              bottom: 0,
              left: 0,
              width: '100%',
              height: '50vh',
              boxSizing: 'border-box',
              borderTop: '1px solid #b28223',
              backgroundColor: '#000',
              transformOrigin: 'bottom',
              transition: 'transform 500ms ease-out',
              transform: `scaleY(${isShow ? 0 : 1})`,
              zIndex: 2000
            }}
          />
        </>
      )}
      {isShow && (
        <>
          <SectionMenu />
          <SectionMainvisual />

          <Section id='about' theme='dark'>
            <SectionTitle align='center' sub='私たちについて'>
              ABOUT
            </SectionTitle>
            <Grid container>
              <Grid item xs={12}>
                <Typography variant='h3'>CREATIVE COMPANY</Typography>
                <Typography align='left' mt={2}>
                  ELEVEN WORK STUDIO
                  はエンジニア、デザイナー、フォトグラファーを中心に多種多様なメンバーが揃ったクリエイティブカンパニーです。
                </Typography>
                <Typography variant='h3' mt={8}>
                  ALL IN ONE SOLUTION
                </Typography>
                <Typography align='left' my={2}>
                  さまざまな分野のエキスパートが一体となり、お客様のニーズに合わせた総合的なソリューションを提供します。
                </Typography>
                <Typography variant='h3' mt={8}>
                  DIVERSITY
                </Typography>
                <Typography align='left' my={2}>
                  チーム全体の多様性と経験の結集により、クリエイティブな視点と戦略的なアプローチを融合させ、革新的なプロジェクトを実現します。
                </Typography>
                <Typography align='center' mt={8}>
                  私たちの多彩なメンバーがお客様のビジョンを実現することをお約束します。
                </Typography>
              </Grid>
            </Grid>
          </Section>

          <Section id='services'>
            <SectionTitle align='center' sub='サービス紹介'>
              SERVICES
            </SectionTitle>
            <Grid container justifyContent='space-between'>
              <Grid item md={5}>
                <SectionTitle variant='h3' sub='システム開発'>
                  WEB DEVELOPMENT
                </SectionTitle>
                <Typography
                  sx={{
                    mb: 4,
                    lineHeight: 2
                  }}
                >
                  高品質なWebシステム開発を提供。カスタムソリューション、使いやすさ、セキュリティに重点を置き、顧客のビジネス目標を実現。信頼性とスケーラビリティにも注力し、将来の成長に対応。タイムリーなコミュニケーションと豊富な経験でお客様の成功をサポート。
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <Box>
                  <StaticImage
                    src='../images/web-development.jpg'
                    alt='Web Development'
                    layout='fullWidth'
                  />
                </Box>
              </Grid>
            </Grid>

            <Grid container justifyContent='space-between' mt={10}>
              <Grid
                item
                md={5}
                order={{
                  md: 2
                }}
              >
                <SectionTitle variant='h3' sub='クリエイティブ制作'>
                  CREATIVE
                </SectionTitle>
                <Typography
                  sx={{
                    mb: 4,
                    lineHeight: 2
                  }}
                >
                  魅力的なデザイン制作を約束します。弊社クリエイティブチームがブランドイメージを具現化し、強力なアイデンティティを構築。最新のトレンドとデザイン原則に基づき、視覚的魅力と機能性を兼ね備えた作品を納品。柔軟な対応と高品質な成果物で、お客様のビジネスの個性を引き立て競争力を向上させます。
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <StaticImage
                  src='../images/creative.jpg'
                  alt='Web Development'
                  layout='fullWidth'
                />
              </Grid>
            </Grid>

            <Grid container justifyContent='space-between' mt={10}>
              <Grid item xs={12} md={5}>
                <Box>
                  <SectionTitle variant='h3' sub='写真撮影'>
                    PHOTOGRAPHY
                  </SectionTitle>
                  <Typography
                    sx={{
                      mb: 4,
                      lineHeight: 2
                    }}
                  >
                    心動かす写真をご提供。弊社が契約しているプロフォトグラファーが独創的な視点と技術で特別な瞬間を捉えます。ポートレートから商品撮影まで幅広いジャンルに対応し、クオリティの高い写真をお届け。撮影後の編集や修正も行い、美しい仕上がりを実現。思い出やビジネスのイメージを鮮明に残すために、私たちの写真撮影サービスをご利用ください。
                  </Typography>
                </Box>
              </Grid>
              <Grid item xs={12} md={6}>
                <Box>
                  <StaticImage
                    src='../images/photography.jpg'
                    alt='Photography'
                    layout='fullWidth'
                  />
                </Box>
              </Grid>
            </Grid>

            <Grid container justifyContent='space-between' mt={10}>
              <Grid
                item
                md={5}
                order={{
                  md: 2
                }}
              >
                <SectionTitle variant='h3' sub='ライフスタイル事業'>
                  LIFESTYLE
                </SectionTitle>
                <Typography
                  sx={{
                    mb: 4,
                    lineHeight: 2
                  }}
                >
                  多彩なライフスタイル事業を展開。健康・ウェルネス、エンターテイメント、旅行など、生活を豊かにするサービスを提供。個別のプランニングとコンサルティングで最適なソリューションを提案。健康クラブやスパ、イベント企画・運営、ツアーオペレーションなどの専門知識と経験を活かし、お客様のライフスタイルを充実させます。私たちのライフスタイル事業でより豊かな人生を実現しませんか？
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <StaticImage
                  src='../images/lifestyle.jpg'
                  alt='Web Development'
                  layout='fullWidth'
                />
              </Grid>
            </Grid>
          </Section>

          <Section id='company' theme='dark'>
            <SectionTitle align='center' sub='会社概要'>
              COMPANY
            </SectionTitle>
            <Grid container justifyContent='center'>
              <Grid item md={8} xs={12}>
                <TableContainer>
                  <Table>
                    <TableBody>
                      {[
                        {
                          key: '会社名',
                          value: (
                            <>
                              イレブンワークスタジオ合同会社
                              <br />
                              <small>(ELEVEN WORK STUDIO LLC)</small>
                            </>
                          )
                        },
                        {
                          key: '代表者名',
                          value: (
                            <>
                              CEO 尾崎 康次郎
                              <br />
                              CTO 森居 良之
                              <br />
                              COO 山崎 智央
                            </>
                          )
                        },
                        {
                          key: '主な事業内容',
                          value: (
                            <>
                              Webシステム開発
                              <br />
                              Web制作
                              <br />
                              AIサービス
                              <br />
                              デザイン制作
                              <br />
                              写真撮影
                            </>
                          )
                        },
                        {
                          key: '本社所在地',
                          value: (
                            <>
                              東京都渋谷区道玄坂1-10-8
                              <br />
                              渋谷道玄坂東急ビル2F-C
                            </>
                          )
                        },
                        {
                          key: '設立年月日',
                          value: '2023年06月30日'
                        },
                        {
                          key: '決算',
                          value: '3月 末日'
                        },
                        {
                          key: '資本金',
                          value: '50万円'
                        }
                      ].map(({ key, value }, i) => (
                        <TableRow
                          key={i}
                          sx={{
                            '&:last-child td, &:last-child th': { border: 0 }
                          }}
                        >
                          <TableCell component='th' scope='row'>
                            <Typography color='primary.dark' fontWeight='bold'>
                              {key}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography color='primary.dark'>
                              {value}
                            </Typography>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </Grid>
            </Grid>
          </Section>

          <Section id='contact'>
            <SectionTitle align='center' sub='お問い合わせ'>
              CONTACT
            </SectionTitle>
            <Grid container justifyContent='center'>
              <Grid item md={6} xs={12}>
                <Box>
                  {[
                    {
                      label: '氏名'
                    },
                    {
                      label: 'メールアドレス'
                    },
                    {
                      label: 'お問い合わせ内容',
                      multiline: true,
                      rows: 4
                    }
                  ].map(({ label, multiline = false, rows = 1 }, i) => (
                    <TextField
                      key={i}
                      variant='outlined'
                      label={label}
                      multiline={multiline}
                      rows={rows}
                      fullWidth
                      sx={{
                        my: 2,
                        '& fieldset': {
                          borderRadius: '0px'
                        }
                      }}
                    />
                  ))}
                  <Button
                    variant='outlined'
                    size='large'
                    sx={{
                      width: '100%',
                      borderRadius: 0
                    }}
                  >
                    送信
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </Section>

          <Footer />
        </>
      )}
    </ThemeProvider>
  );
};

export default IndexPage;

export const Head = () => <Seo />;
